/* UI elements "Cards" */
.article-preview-card__thumbnail {
    width: 100%;
    height: 144px;
    object-fit: cover;
    object-position: center;
    border-radius: 4px;
}

.article-preview-card__link {
    display: flex;
    align-items: center;
    margin: 12px 0 0 0;
    color: inherit;
    text-decoration: none;
}

.article-preview-card__link>*+* {
    margin: 0 0 0 10px;
}

.article-preview-card__link-label {
    font-size: 14px;
    line-height: 17px;
    color: inherit;
}

.article-preview-card__link-icon {
    font-size: 24px;
    color: inherit;
}

.article-preview-card__text {
    margin: 12px 0 0 0;
    opacity: 0.5;
}

/* UI elements "links" */
.site-logo {
    text-decoration: none;
}

.site-logo__img {
    width: 100%;
    height: 100%;
    object-fit: contain;
    object-position: center;
}

.account {
    display: flex;
    align-items: center;
    justify-content: center;
}

.account__link {
    font-size: 24px;
    color: #383838;
    text-decoration: none;
    line-height: 1;
}

.account__icon {
    color: inherit;
    line-height: 1;
}

.shopping-cart {
    display: flex;
    align-items: center;
    justify-content: center;
}

.shopping-cart__link {
    font-size: 24px;
    line-height: 1;
    color: #787d46;
    text-decoration: none;
}

.shopping-cart__icon {
    color: inherit;
    line-height: 1;
}

.color-palette {
    display: flex;
    align-items: center;
}

.color-palette__link {
    display: flex;
    align-items: center;
    color: inherit;
    text-decoration: none;
}

.color-palette__icon {
    font-size: 24px;
    color: #787d46;
}

.color-palette__label {
    display: none;
}

.phone-link {
    color: inherit;
    font-size: 16px;
    line-height: 20px;
    text-decoration: none;
}

