/* UI elements "Navigations" */
.header-menu__button {
    display: flex;
    align-items: flex-end;
    flex-direction: column;
    justify-content: space-between;
    width: 24px;
    height: 16px;
    border: none;
    background-color: transparent;
    cursor: pointer;
}

@media only screen and (min-width: 768px) {
    .header-menu__button {
        width: 32px;
        height: 22px;
    }
}

@media only screen and (min-width: 1280px) {
    .header-menu__button {
        display: none;
    }
}

.header-menu__button>span {
    height: 2px;
    border-radius: 2px;
    background-color: #383838;
}

@media only screen and (min-width: 768px) {
    .header-menu__button>span {
        height: 3px;
    }
}

.header-menu__button>span:nth-child(1) {
    width: 75%;
}

.header-menu__button>span:nth-child(2) {
    width: 100%;
}

.header-menu__button>span:nth-child(3) {
    width: 50%;
}

.header-menu__body {
    position: fixed;
    top: 88px;
    left: 0;
    width: 100%;
    height: calc(100vh - 88px);
    padding: 24px 15px 24px 15px;
    background-color: #ffffff;
    z-index: -1;
    overflow: scroll;
}

.header-menu__list {
    list-style-type: none;
}

.header-menu__list>*+* {
    margin: 20px 0 0 0;
}

.header-menu__list-link {
    display: flex;
    align-items: center;
    justify-content: space-between;
    text-decoration: none;
    color: inherit;
}

.header-menu__list-link-text {
    font-size: 16px;
    line-height: 20px;
    color: inherit;
}

.header-menu__list-link-icon {
    color: inherit;
    font-size: 24px;
    transition: transform 0.5s ease 0s;
}

.header-menu__list-link-icon.--active {
    transform: rotate(90deg);
}

.header-menu__submenu {
    display: none;
}

.header-menu__submenu.--active {
    display: block;
}

.header-submenu {
    margin: 18px 0 0 0;
}

.header-submenu__list {
    list-style-type: none;
}

.header-submenu__list>*+* {
    margin: 16px 0 0 0;
}

.header-submenu__list-link {
    font-size: 14px;
    line-height: 17px;
    text-decoration: none;
    color: #383838;
    opacity: 0.5;
}

.header-submenu__articles {
    margin: 16px 0 0 0;
}

.header-submenu__articles>*+* {
    margin: 24px 0 0 0;
}

/* Block "header" */
.header {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    background-color: white;
    z-index: 100;
}

.header__inner {
    display: flex;
    align-items: center;
    padding: 24px 0 24px 0;
    border-bottom: 1px solid rgba(56, 56, 56, 0.5);
}

.header__logo {
    height: 39px;
    width: 130px;
    display: flex;
    align-items: stretch;
}

@media only screen and (min-width: 768px) {
    .header__logo {
        width: 160px;
        height: 48px;
    }
}

.header__menu {
    order: 1;
    flex: 0 0 auto;
    margin: 0 0 0 18px;
}

@media only screen and (min-width: 768px) {
    .header__menu {
        order: 0;
        margin: 0 0 0 30px;
    }
}

.header__color-palette {
    display: none;
}

@media only screen and (min-width: 768px) {
    .header__color-palette {
        display: block;
        flex: 0 0 auto;
        margin: 0 0 0 auto;
    }
}

.header__contacts {
    display: none;
}

@media only screen and (min-width: 768px) {
    .header__contacts {
        display: block;
        flex: 0 0 auto;
        margin: 0 0 0 32px;
    }
}

@media only screen and (min-width: 1280px) {
    .header__contacts {
        margin: 0 0 0 24px;
    }
}

@media only screen and (min-width: 1600px) {
    .header__contacts {
        margin: 0 0 0 32px;
    }
}

.header__contacts-link-group {
    display: flex;
    align-items: center;
}

.header__contacts-link-group>*+* {
    margin: 0 0 0 16px;
}

.header__account-and-cart {
    display: flex;
    align-items: center;
    margin: 0 0 0 auto;
}

.header__account-and-cart>*+* {
    margin: 0 0 0 18px;
}

@media only screen and (min-width: 768px) {
    .header__account-and-cart {
        margin: 0 0 0 32px;
    }
}

@media only screen and (min-width: 1600px) {
    .header__account-and-cart {
        margin: 0 0 0 50px;
    }
}

